* {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  color: inherit;
}
ul,
li {
  list-style-type: none;
  color: ivory;
}
body {
  color: #808080;
}
h2 {
  padding: 20px;
}

* {
  padding: 0px;
  margin: 0px;
  border: 0px;
}
*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
:focus,
:active {
  outline: none;
}
a:focus,
a:active {
  outline: none;
}
aside,
nav,
footer,
header,
section {
  display: block;
}
html {
  font-size: inherit;
}
html,
body {
  height: 100%;
  min-width: 320px;
}
body {
  line-height: 1;
  font-family: inherit;
  text-rendering: optimizeLegibility;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
input,
button,
textarea {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background: inherit;
}
input::-ms-clear {
  display: none;
}
button {
  cursor: pointer;
  background-color: inherit;
}
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}
a,
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
ul li {
  list-style: none;
}
img {
  vertical-align: top;
}

.app__wrapper {
  grid-template-columns: 1fr;
  grid-gap: 20px;
  margin: 0 auto;
  display: grid;
  grid-template-rows: 60px 1fr 60px;
  height: 100vh;
  max-width: 1024px;
}
.app__wrapper_content {
  padding: 5px;
  grid-area: main;
  box-shadow: 1px 10px 10px rgba(150, 150, 150, 0.2);
  color: silver;
  border-radius: 2em;
}

@media (min-width: 900px) {
  .app__wrapper {
    grid-template-areas:
      "header header "
      "sidebar main "
      "footer footer" !important;
    grid-template-columns: 1fr 5fr;
  }
  .app__wrapper_content {
    padding: 0;
  }
}
